
import XButton from 'vux/src/components/x-button/index.vue'
import CheckIcon from 'vux/src/components/check-icon/index.vue'


export default {
components: {
XButton,
CheckIcon
},
data() {
return {
height: "0px",
footHeight: "0px",
isagree:false
};
},
methods: {







angre() {
if(!this.isagree){
this.$vux.confirm.show({
content: "您需同意《蓉E充充电注册服务协议》方可使用本软件",
confirmText: "确认",
showCancelButton: false
});
}else{
localStorage.setItem("potocol", true);
this.$router.push("/home/index");
}

}
},
created() {
var potocol = localStorage.getItem("potocol");
if (potocol) {
this.$router.push("/home/index");
}
this.height = this.minHeight + "px";
this.footHeight = (this.minHeight / 100) * 88 + "px";
}
};



